.menuItem {
  position: relative;
  display: inline-block;
  padding: 0 50px;
  border-right: 1px solid rgba(179, 179, 179, 0.2);
  line-height: 50px; }
  .menuItem:first-child {
    border-left: 1px solid rgba(179, 179, 179, 0.2); }
  .menuItem:hover {
    background-color: rgba(204, 204, 204, 0.2);
    cursor: pointer; }
  .menuItem a {
    position: relative;
    display: block;
    width: 100%;
    height: 100%; }
